/** Extracts run data from each samples and sorts by reverse date */
function GetRunData(hemeSamples) {
    let runData = [];
    let i = 0;
    // for each sample, get the run data
    hemeSamples.forEach((sample) => {
        const runDates = sample.analysisDates;
        runDates.forEach((datestring, dateCol_index) => {
            const dateref = 'date' + (dateCol_index + 1);
            let data = GetRunDatum(sample, new Date(datestring), dateref);
            runData.push(data);
            i++;
        });
    });
    // sort runData by reversed date (recent on top)
    runData = runData.sort((a, b) => {
        return b.date.getTime() - a.date.getTime();
    });
    // add seq to sorted runData runinfo
    runData.forEach((run, index) => {
        run.subgroups['runinfo']['Seq'] = runData.length - index;
    });
    return runData;
}
exports.GetRunData = GetRunData;
function GetRunDatum(hemeSample, date, dateref) {
    const subgroupsItems = hemeSample.subgroups;
    const runinfo = {
        'Day': date.toLocaleDateString("en-US"),
        'Time': date.toLocaleTimeString(),
    };
    let data = {
        id: hemeSample.id,
        label: hemeSample.label,
        date: date,
        dateref: dateref,
        subgroups: { runinfo: runinfo }
    };
    for (const key_subgroupid in subgroupsItems) {
        const items = subgroupsItems[key_subgroupid];
        const obj = data.subgroups[key_subgroupid] || {};
        let parseditems = {};
        for (const itemkey in items) {
            const item = items[itemkey];
            const key = item['item'];
            if (item['description'])
                parseditems[key] = item['description'];
            else if (item[dateref])
                parseditems[key] = item[dateref];
            else if (hemeSample['presenting'] == hemeSample.analysisDate)
                parseditems[key] = item['presenting'];
            else
                parseditems[key] = '';
        }
        data.subgroups[key_subgroupid] = Object.assign(Object.assign({}, obj), parseditems);
    }
    return data;
}
exports.GetRunDatum = GetRunDatum;
